@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@500&display=swap');

* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: 'Poppins', sans-serif;
}
.container {
    width: 100vw;
    height: 100vh;
    display: flex;
    align-items: center;
    justify-content: center;
    background: linear-gradient(135deg, #00B3CC, #003CC5);
    
    .box {
        width: 60%;
        height: 80%;
        background: #fff;
        border: none;
        outline: none;
        border-radius: 25px;
        display: flex;
        flex-direction: column;
        align-items: center;

        #btn {
            height: 10%;
            width: 25%;
            margin-top: 30px;
            background: linear-gradient(135deg, #FF0076, #590FB7);
            border-radius: 25px;
            color: #fff;
            font-weight: bold;
            letter-spacing: 2px;
            border: none;
            outline: none;
            box-shadow: 8px 10px 15px #fb96ff;
        }
        #btn:active {
            transform: scale(1.04);
            opacity: 70%;
        }

        .color-box {
            margin-top: 50px;
            height: 200px;
            width: 200px;
            border-radius: 25px;
            background: linear-gradient(135deg, #003CC5, #00B3CC);
        }

        .hex-val {
            margin-top: 20px;
            color: #FF0076;
        }

        h2 {
            margin-top: 30px;
            color: #590FB7;
            span {
                font-size: 18px;
            }
        }
    }
    
}

